
.em {
  display: inline-block;
  width: 1em;
}
.em_5 {
  display: inline-block;
  width: .5em;
}
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
dt,
dd,
input,
form {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
body {
  background-color: #f0eff6;
  color: #372f2c;
  font-family: "Microsoft Yahei";
}
.fl {
  float: left;
}
.fr {
  float: right;
}
#index {
  background: #f0eff5;
}
#index .header .header-t {
  background: #4782ea;
  height: 100px;
  text-align: center;
  padding-top: 30px;
  color: #fff;
}
#index .header .header-t h3 {
  margin-top: 10px;
  font-size: 24px;
}
#index .header .header-m {
  background: #568eef;
  overflow: hidden;
  padding: 10px 0;
  color: #fff;
}
#index .header .header-m li {
  float: left;
  text-align: center;
  width: 33%;
}
#index .header .header-m li:nth-child(2) {
  border-left: 1px solid #4782ea;
  border-right: 1px solid #4782ea;
}
#index .header .header-b {
  background: #fff;
  overflow: hidden;
  padding: 10px 0;
  text-align: center;
}
#index .header .header-b li {
  float: left;
  width: 49.5%;
}
#index .header .header-b li:last-child {
  border-left: 1px solid #aaa;
}
#index .device {
  margin-top: 20px;
  background: #fff;
  padding: 20px;
  border-bottom: 1px solid #f0eff5;
}
#index .device .num {
  color: #999;
}
#index .regist {
  overflow: hidden;
  background: #fff;
}
#index .regist li {
  float: left;
  text-align: center;
  padding: 20px 0;
  width: 49.5%;
}
#index .regist li p:last-child {
  color: #999;
}
#index .regist li:last-child {
  border-left: 1px solid #ddd;
}
#index .person {
  margin-top: 20px;
  padding: 20px;
  background: #fff;
}
#index .person div .radius {
  border-radius: 10px;
  background: #fb433d;
  padding: 0 10px;
}
#index .person p {
  color: #999;
}



